:root {
  --bg: #181926;
  --bg-alt: #23243a;
  --header-footer: #1a1b26;
  --text: #c0caf5;
  --accent: #7aa2f7;
  --accent2: #7dcfff;
  --accent3: #bb9af7;
  --card-shadow: 0 4px 32px 0 #16161e33;
  --border: #292e42;
  --nav-bg: #23243a;
  --nav-btn-bg: #23243a;
  --nav-btn-hover: #7aa2f7;
  --nav-btn-hover-text: #181926;
  --footer-link: #7dcfff;
  --footer-link-hover: #bb9af7;
  --decorative: #7aa2f7;
}
:root[data-theme='light'] {
  --bg: #f7f7fa;
  --bg-alt: #fff;
  --header-footer: #f2f2f7;
  --text: #23243a;
  --accent: #3b82f6;
  --accent2: #06b6d4;
  --accent3: #a78bfa;
  --card-shadow: 0 4px 32px 0 #b6c2e033;
  --border: #e5e7eb;
  --nav-bg: #f2f2f7;
  --nav-btn-bg: #e5e7eb;
  --nav-btn-hover: #3b82f6;
  --nav-btn-hover-text: #fff;
  --footer-link: #06b6d4;
  --footer-link-hover: #a78bfa;
  --decorative: #3b82f6;
}
body {
  background: var(--bg);
  color: var(--text);
  font-family: 'Fira Code', 'JetBrains Mono', 'Menlo', 'Monaco', monospace;
  margin: 0;
  line-height: 1.7;
  transition: background 0.3s, color 0.3s;
}
.custom-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: var(--header-footer);
  color: var(--accent);
  padding: 2.2em 0 1.2em 0;
  border-radius: 0 0 18px 18px;
  box-shadow: 0 2px 8px 0 #16161e22;
  position: sticky;
  top: 0;
  z-index: 100;
  margin-bottom: 1.5em;
  transition: background 0.3s, color 0.3s;
}
.header-title {
  font-size: clamp(2.2rem, 5vw, 3.5rem);
  font-weight: 900;
  letter-spacing: 0.04em;
  font-family: 'Fira Code', monospace;
  text-align: center;
  margin-bottom: 0.5em;
  text-shadow: 0 2px 16px var(--accent)55, 0 1px 0 var(--bg-alt);
}
.header-nav {
  display: flex;
  gap: 2.5em;
  justify-content: center;
  margin-bottom: 0.2em;
}
.header-nav a {
  color: var(--accent2);
  font-size: 1.2em;
  font-weight: 600;
  text-decoration: none;
  padding: 0.5em 1.5em;
  border-radius: 8px;
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
  box-shadow: 0 2px 8px 0 #16161e22;
  background: var(--nav-btn-bg);
  margin-bottom: 0.2em;
  display: flex;
  align-items: center;
  border: none;
}
.header-nav a:hover {
  background: var(--nav-btn-hover);
  color: var(--nav-btn-hover-text);
  box-shadow: 0 4px 16px 0 var(--accent)44;
}
.theme-toggle {
  background: var(--nav-btn-bg);
  border: none;
  border-radius: 8px;
  padding: 0.5em 1.2em;
  margin-left: 0.2em;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
  box-shadow: 0 2px 8px 0 #16161e22;
  color: var(--accent2);
}
.theme-toggle:hover {
  background: var(--nav-btn-hover);
  color: var(--nav-btn-hover-text);
  box-shadow: 0 4px 16px 0 var(--accent)44;
}
.theme-toggle .theme-icon {
  display: flex;
  align-items: center;
  justify-content: center;
}
.main-container {
  max-width: 900px;
  margin: 0 auto 0 auto;
  padding: 0 1.5em;
}
.custom-main {
  background: var(--bg-alt);
  border-radius: 18px;
  box-shadow: var(--card-shadow);
  padding: 2.5em 2em 2.5em 2em;
  min-height: 60vh;
  margin: 0 auto 3em auto;
  position: relative;
  overflow: hidden;
  transition: background 0.3s, color 0.3s;
}
.custom-main::before {
  content: '';
  position: absolute;
  top: -80px;
  right: -80px;
  width: 180px;
  height: 180px;
  background: radial-gradient(circle, var(--decorative) 0%, var(--bg-alt) 80%);
  opacity: 0.13;
  filter: blur(16px);
  z-index: 0;
}
.custom-footer {
  background: var(--header-footer);
  color: var(--accent);
  text-align: center;
  padding: 2em 0 2.5em 0;
  border-radius: 18px 18px 0 0;
  box-shadow: 0 -2px 8px 0 #16161e22;
  margin-top: 3em;
  transition: background 0.3s, color 0.3s;
}
.footer-title {
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 0.7em;
}
.footer-links {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  gap: 2em;
}
.footer-links a {
  color: var(--footer-link);
  font-size: 1.1em;
  text-decoration: none;
  transition: color 0.2s;
}
.footer-links a:hover {
  color: var(--footer-link-hover);
}
h1, h2, h3, h4, h5, h6 {
  color: var(--accent);
  margin-top: 2.2rem;
  margin-bottom: 1.1rem;
  font-weight: 700;
  letter-spacing: 0.01em;
}
h1 {
  font-size: 2.3rem;
  border-bottom: 2px solid var(--border);
  padding-bottom: 0.5rem;
}
h2 {
  font-size: 1.5rem;
  border-left: 4px solid var(--accent);
  padding-left: 0.7rem;
}
h3 {
  font-size: 1.15rem;
}
a {
  color: var(--accent2);
  text-decoration: none;
  transition: color 0.2s;
}
a:hover {
  color: var(--accent3);
}
code, pre {
  background: var(--border);
  color: var(--text);
  border-radius: 4px;
  padding: 0.2em 0.4em;
}
pre {
  padding: 1em;
  overflow-x: auto;
}
hr {
  border: none;
  border-top: 1px solid var(--border);
  margin: 2em 0;
}
ul, ol {
  margin-left: 2em;
}
blockquote {
  background: var(--border);
  color: var(--text);
  border-left: 4px solid var(--accent);
  margin: 1.5em 0;
  padding: 1em 1.5em;
  border-radius: 6px;
  font-style: italic;
}
.nav-links {
  display: flex;
  justify-content: space-between;
  margin-top: 3em;
  margin-bottom: 2em;
  gap: 2em;
}
.nav-links a {
  display: inline-block;
  background: var(--border);
  color: var(--accent2);
  border: none;
  border-radius: 8px;
  padding: 0.7em 1.6em;
  font-size: 1.05em;
  font-family: inherit;
  font-weight: 600;
  box-shadow: 0 2px 8px 0 #16161e22;
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
  text-align: center;
}
.nav-links a:hover {
  background: var(--accent);
  color: var(--nav-btn-hover-text);
  box-shadow: 0 4px 16px 0 var(--accent)44;
}

/* Modern, visually appealing markdown table styles */
.table-responsive, table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  margin: 2em 0;
  background: var(--bg-alt);
  border-radius: 12px;
  box-shadow: 0 2px 12px 0 #16161e22;
  overflow: hidden;
}
thead {
  background: var(--accent);
  color: var(--nav-btn-hover-text);
}
thead th {
  padding: 0.9em 1.2em;
  font-size: 1.08em;
  font-weight: 700;
  text-align: left;
  border-bottom: 2px solid var(--border);
}
tbody tr {
  transition: background 0.2s;
}
tbody tr:nth-child(even) {
  background: var(--bg);
}
tbody tr:nth-child(odd) {
  background: var(--bg-alt);
}
td {
  padding: 0.8em 1.2em;
  border-bottom: 1px solid var(--border);
  font-size: 1em;
}
tbody tr:last-child td {
  border-bottom: none;
}
table {
  border-radius: 12px;
  overflow: hidden;
}
/* Responsive: horizontal scroll for tables on small screens */
@media (max-width: 700px) {
  .custom-main table {
    display: block;
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    border-radius: 12px;
    box-shadow: 0 2px 12px 0 #16161e22;
  }
  .custom-main thead, .custom-main tbody, .custom-main th, .custom-main td, .custom-main tr {
    display: table;
    width: 100%;
  }
  .custom-main th, .custom-main td {
    min-width: 120px;
    white-space: nowrap;
  }
}

@media (max-width: 700px) {
  .custom-header, .custom-footer {
    padding-left: 1em;
    padding-right: 1em;
  }
  .custom-main {
    padding: 1em 0.5em;
  }
  .header-nav {
    gap: 1em;
  }
  .footer-links {
    gap: 1em;
  }
  .main-container {
    padding: 0 0.2em;
  }
} 

/* Syntax highlighting for code blocks (Rouge, One Dark/Light inspired) */
:root {
  --code-bg-dark: #292e42;
  --code-bg-light: #f5f7fa;
  --code-fg-dark: #c0caf5;
  --code-fg-light: #23243a;
  --code-keyword: #7aa2f7;
  --code-string: #9ece6a;
  --code-number: #ff9e64;
  --code-function: #bb9af7;
  --code-variable: #7dcfff;
  --code-comment: #565f89;
  --code-type: #2ac3de;
  --code-constant: #ff5370;
}
:root[data-theme='light'] {
  --code-bg-dark: #f5f7fa;
  --code-bg-light: #fff;
  --code-fg-dark: #23243a;
  --code-fg-light: #23243a;
  --code-keyword: #2563eb;
  --code-string: #059669;
  --code-number: #b45309;
  --code-function: #7c3aed;
  --code-variable: #0ea5e9;
  --code-comment: #64748b;
  --code-type: #0ea5e9;
  --code-constant: #dc2626;
}
pre, code, .highlight {
  background: var(--code-bg-dark);
  color: var(--code-fg-dark);
  font-family: 'Fira Code', 'JetBrains Mono', 'Menlo', 'Monaco', monospace;
  font-size: 1em;
  border-radius: 6px;
  padding: 0.2em 0.4em;
  overflow-x: auto;
  transition: background 0.3s, color 0.3s;
}
:root[data-theme='light'] pre, :root[data-theme='light'] code, :root[data-theme='light'] .highlight {
  background: var(--code-bg-light);
  color: var(--code-fg-light);
}
.highlight .c, .highlight .cm, .highlight .c1, .highlight .cs { color: var(--code-comment); font-style: italic; }
.highlight .k, .highlight .kd, .highlight .kp, .highlight .kr, .highlight .kt { color: var(--code-keyword); }
.highlight .s, .highlight .s1, .highlight .s2, .highlight .sb, .highlight .sc, .highlight .sd, .highlight .se, .highlight .sh, .highlight .si, .highlight .sx, .highlight .sr, .highlight .ss { color: var(--code-string); }
.highlight .mi, .highlight .mf, .highlight .mh, .highlight .il, .highlight .mo { color: var(--code-number); }
.highlight .nf, .highlight .fm { color: var(--code-function); }
.highlight .nv, .highlight .vc, .highlight .vg, .highlight .vi, .highlight .vm { color: var(--code-variable); }
.highlight .nc, .highlight .no, .highlight .nd, .highlight .ne, .highlight .ni, .highlight .nl, .highlight .nn, .highlight .nt, .highlight .nx, .highlight .bp { color: var(--code-type); }
.highlight .m, .highlight .mb, .highlight .mf, .highlight .mh, .highlight .mi, .highlight .mo { color: var(--code-number); }
.highlight .o, .highlight .ow { color: var(--code-constant); }
.highlight .err { color: #fff; background: #dc2626; }
pre code { background: none; padding: 0; } 

:root[data-theme='gradient'] body {
  background: linear-gradient(120deg, #f6d365 0%, #fda085 20%, #7aa2f7 50%, #bb9af7 70%, #43e97b 85%, #38f9d7 100%);
  background-size: 300% 300%;
  animation: gradient-move 12s ease-in-out infinite;
}
:root[data-theme='gradient'] .custom-header,
:root[data-theme='gradient'] .custom-footer {
  background: rgba(26,27,38,0.85);
  backdrop-filter: blur(2px);
}
:root[data-theme='gradient'] .custom-main {
  background: rgba(35,36,58,0.92);
  box-shadow: 0 4px 32px 0 #7aa2f755;
}

/* Animated gradient overlays for left/right sides */
.gradient-side {
  position: fixed;
  top: 0; bottom: 0;
  width: 18vw;
  z-index: 0;
  pointer-events: none;
  opacity: 0.7;
  background: linear-gradient(160deg, #f6d365 0%, #fda085 30%, #7aa2f7 70%, #bb9af7 100%);
  filter: blur(32px);
  animation: gradient-move 16s linear infinite alternate;
}
.gradient-side.left {
  left: 0;
  background: linear-gradient(120deg, #f6d365 0%, #fda085 30%, #7aa2f7 70%, #bb9af7 100%);
}
.gradient-side.right {
  right: 0;
  background: linear-gradient(-120deg, #43e97b 0%, #38f9d7 40%, #7aa2f7 80%, #bb9af7 100%);
}
@keyframes gradient-move {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* Ensure main content is above gradient overlays */
body, .main-container, .custom-header, .custom-footer {
  position: relative;
  z-index: 1;
} 